<template>
  <div class="messages">
    <div class="messages__window">
      <Header :src="'alex'" :name="'Александр'" :status="'Онлайн'"/>
      <Main :sendId="2" :message="message"/>
      <Footer :sendId="2" @newMess="newMess"/>
    </div>

    <div class="messages__window">
      <Header :src="'jane'" :name="'Евгений'" :status="'Онлайн'"/>
      <Main :sendId="1" :message="message"/>
      <Footer :sendId="1" @newMess="newMess"/>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Main from "@/components/Main.vue";
import Footer from "@/components/Footer.vue";
export default {
  components: {
    Header,
    Main,
    Footer,
  },
  data(){
    return{
      message: []
    }
  },
  created(){
    const messStorage = localStorage.message ? JSON.parse(localStorage.message) : []
    this.message = messStorage
  },
  methods:{
    newMess(mess){
      this.message.push(mess)
      localStorage.message = JSON.stringify(this.message)
    }
  }
};
</script>

