<template>
  <footer class="footer">
    <textarea 
    rows="1"
    placeholder="Написать сообщение..."
    class="footer__edit"
    v-model="message"
    >
    </textarea>
    <button class="footer__btn" v-if="message.length == 0" @click="window = true">
        <img src="@/assets/img/photo.svg" alt="">
    </button>
    <button class="footer__btn" v-else @click="sendMess">
        <img src="@/assets/img/send.svg" alt="">
    </button>
    
    <div class="window" v-if="window" @click="window = false">
        <div class="window__body" @click.stop.prevent>
            <h2 class="window__title">Отправить картинку</h2>
             <div class="window__form">
                <label>
                    <span>URL</span>
                    <input v-model="photo">
                </label>
                <label>
                    <span>Комментарий</span>
                    <textarea v-model="comment"></textarea> 
                </label>
             </div>
             <div class="window__footer">
                <button class="btn" @click="window = false">Отмена</button>
                <button class="btn" @click="sendMessPhoto">ОТПРАВИТЬ</button>
             </div>
        </div>
    </div>
  </footer>
</template>

<script>
export default {
    props: ['sendId'],
    data(){
        return{
            message: '',
            window: false,
            photo: '',
            comment: ''
        }
    },
    methods:{
        sendMessPhoto(){
            const time = `${new Date().getHours()}:${new Date().getMinutes()}`
            if (this.photo.length > 0) {       
                const mess = {}
                mess.body = this.comment.trim()
                mess.sendId = this.sendId
                mess.photo = this.photo.trim()
                mess.time = time
                this.$emit('newMess', mess)
                this.comment = this.photo = ''
                this.window = false
            }
        },
        sendMess(){
            const time = `${new Date().getHours()}:${new Date().getMinutes()}`
            if (this.message.length > 0) {       
                const mess = {}
                mess.body = this.message.trim()
                mess.sendId = this.sendId
                mess.time = time
                this.$emit('newMess', mess)
                this.message = ''
            }
        },
    }
}
</script>

